a{
    text-decoration: none;
    color: #FFF;
    border-bottom: 2px solid transparent;
    padding-bottom: 6px;
    font-weight: 500;
  }
  a.active{
    border-bottom: 2px solid #FFF;
    font-weight: 500;
  }
  #root{
    height: 100vh;
    overflow: hidden;
  }
  .quiz{
    min-height: 340px;
    width: 500px;
    border-radius: 5px;
    background-color: #FFF;
  }
  .fa-circle-check{
    color: #55F;
  }
  .fa-circle{
    color:#DDD;
  }
  .options{
    cursor: pointer;
    margin: 7px 5px;
  }
  .Votes{
    font-size: 12px;
    white-space: pre-wrap;
  }

  .radio{
    height: 30px;
    width: 30px;
    background-color: red !important;
  }